.accordion {
  margin-top: 115px;
  padding-bottom: 85px;
}

.accordion__title {
  text-align: center;
}

.accordion-item {
  position: relative;
  margin-bottom: 20px;
}

.accordion-item__input {
  position: absolute;
  top: 0;
  right: 0;
  opacity: 0;
}

.accordion-item__trigger {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
  background: rgba(129, 144, 154, 0.2);
  transition: 1s;
  color: #373D54;
  font-size: 18px;
  font-family: "Roboto";
  font-style: normal;
}
.accordion-item__trigger::after {
  content: url(../images/plus.svg);
  float: right;
  /* Размещаем по правому краю */
  background-color: #fff;
  padding: 5px;
  color: #E6E9EB;
  width: 30px;
  text-align: center;
  height: 30px;
}

.accordion-item__content {
  padding: 20px;
  border: 1px solid;
  border-top: none;
  display: none;
}

.accordion-item__input:checked ~ .accordion-item__trigger {
  background-position: bottom 20px right 100px;
  background-color: #2D2CAB;
  color: #FFFFFF;
}
.accordion-item__input:checked ~ .accordion-item__trigger::after {
  content: url(../images/minus.svg);
  color: #2D2CAB;
  width: 30px;
  height: 30px;
  padding: 0;
}

.accordion-item__input:checked ~ .accordion-item__content {
  display: block;
  color: #81909A;
  font-size: 16px;
}

